import React, { useEffect, useState } from "react";
import './Login.scss'
import closeEye from '../../assets/images/application/close-eye.png'
import logo from '../../assets/images/application/WCS_PS_Logo.png'
import { Link, useParams } from "react-router-dom";
import * as yup from 'yup'
import { ErrorMessage, Formik, Form } from "formik";
import { Modal } from "react-bootstrap";
import { auto } from "@popperjs/core";

const defaultvalues = {
    email: '',
    password: ''
}

const validationSchema = yup.object().shape({
    email: yup.string().required('email address required').email('enter valid email address'),
    password: yup.string().required('password required'),
})

const LoginPage = (props) => {
    const { type } = useParams()
    const [showPassword, setShowPassword] = useState(false)
    const [isResetPassword,setIsResetPassword] = useState(false)
    const [resetEmail,setResetEmail] = useState('')

    const handleSubmit = (value, { resetForm }) => {
        if (type === 'admin') {
            console.log({ ...value, type: type })
        } else {
            console.log({ ...value, type: 'PML' })
        }
        resetForm()
    }
    const handleResetPassword = (e)=>{
        e.preventDefault()
        console.log(resetEmail)
        setResetEmail('')
    }
    return (
        <>
            <div className="container-fluid apply-online-process-beta">
                <div className="main-beta responsivemain-beta">
                    <Link to={'/'} className="navbar-brand position-relative">
                        <img src={logo} className="navbar-brand-img"
                            style={{ display: 'block !important', width: '150px' }} />
                    </Link>
                    <div className="main-form">
                        <div id="msform" className="card msform-bgwhiteresponsive maxwidth-custome">

                            <div className="mb-5 text-center">
                                <h3 className="login-title-wlcm mt-2 mb-0">{type === 'admin' ? 'Hi WCS Property Solutions Team' : 'PML Portal'}</h3>
                                <p className="login-subtitle">Please enter your details to continue.</p>
                            </div>
                            <Formik
                                initialValues={defaultvalues}
                                validationSchema={validationSchema}
                                onSubmit={handleSubmit}>
                                {({ values, handleChange, handleBlur, isSubmitting, submitCount, setFieldValue }) => (
                                    <Form>
                                        <div className="mt-3 mb-4 form-group">
                                            <div>
                                                <div>
                                                    <label for="email" className="input-label text-left">Email Address *</label>
                                                </div>
                                                <input
                                                    name="email"
                                                    className="input-custome-login"
                                                    placeholder="Enter your Email Address"
                                                    type="email"
                                                    onChange={handleChange}
                                                    onBlur={handleBlur}
                                                    value={values.email}
                                                />
                                                <ErrorMessage className="validation-error" name='email' component='div' />
                                            </div>
                                        </div>
                                        <div className="mt-3 form-group ">
                                            <div className="position-relative">
                                                <div>
                                                    <label for="password" className="input-label text-left">Password *</label>
                                                </div>
                                                <input
                                                    type={showPassword ? 'text' : 'password'}
                                                    name="password"
                                                    className="input-custome-login"
                                                    placeholder="Enter your password"
                                                    value={values.password}
                                                    onChange={handleChange}
                                                    onBlur={handleBlur} />

                                                <div className="icon-eye-position">
                                                    {showPassword ?
                                                        <span><img src={closeEye}
                                                            alt="close_eye" style={{ height: '23px' }} onClick={() => setShowPassword(!showPassword)} />
                                                        </span>
                                                        :
                                                        <span><svg xmlns="http://www.w3.org/2000/svg"
                                                            width="21" height="18" fill="#A6AEBE" className="bi bi-eye" viewBox="0 0 16 16" onClick={() => setShowPassword(!showPassword)}>
                                                            <path
                                                                d="M16 8s-3-5.5-8-5.5S0 8 0 8s3 5.5 8 5.5S16 8 16 8zM1.173 8a13.133 13.133 0 0 1 1.66-2.043C4.12 4.668 5.88 3.5 8 3.5c2.12 0 3.879 1.168 5.168 2.457A13.133 13.133 0 0 1 14.828 8c-.058.087-.122.183-.195.288-.335.48-.83 1.12-1.465 1.755C11.879 11.332 10.119 12.5 8 12.5c-2.12 0-3.879-1.168-5.168-2.457A13.134 13.134 0 0 1 1.172 8z" />
                                                            <path d="M8 5.5a2.5 2.5 0 1 0 0 5 2.5 2.5 0 0 0 0-5zM4.5 8a3.5 3.5 0 1 1 7 0 3.5 3.5 0 0 1-7 0z" />
                                                        </svg></span>
                                                    }
                                                </div>
                                                <ErrorMessage className="validation-error" name='password' component='div' />
                                            </div>
                                        </div>
                                        <div className="text-right">
                                            <a className="mx-1 reset" style={{ color: '#2C3E50' }} href="javascript:void(0)"
                                                tooltip="Reset Password" placement="top" onClick={()=>setIsResetPassword(true)}>
                                                <span className=" btn-inner--text" style={{ fontSize: '12px' }}>Reset Password</span>
                                            </a>
                                        </div>
                                        <div className="mt-3">
                                            <button type="submit" className="px-5 btn btn w-100 text-white" style={{ backgroundColor: '#d9b451' }}>Login</button>
                                        </div>
                                        <div className="text-right">
                                            <a className="mx-1 reset" style={{ color: '#2C3E50' }} href="javascript:void(0)"
                                                placement="top">
                                                <span className=" btn-inner--text" style={{ fontSize: '12px' }}>Don't have account? <Link to='/signup'>Sign Up</Link></span>
                                            </a>
                                        </div>
                                    </Form>
                                )}
                            </Formik>
                        </div>
                    </div>

                </div>
            </div>
            <Modal show={isResetPassword} size="sm" style={{transition:'none'}}  className="modal-overlay" centered>
                <Modal.Body >
                    <h4 class="text-center">Reset Password</h4>
                    {/* <p class="mb-3 text-center">Please check your email<br />for the one time code to reset your password.</p> */}
                    <form onSubmit={handleResetPassword}>
                        <div className="form-group mt-4">
                            <label for="password" className="input-label text-left">Email Address *</label>
                            <input 
                            required
                            type="email" 
                            className="form-control"
                            placeholder="Enter registered email address"
                            name="resetEmail"
                            value={resetEmail}
                            onChange={(e)=> setResetEmail(e.target.value)} 
                            />
                        </div>
                        <div class="mt-4 text-center">
                            <button type="button" class="btn btn-danger mr-2" onClick={()=>setIsResetPassword(!isResetPassword)}>Cancel</button>
                            <button type="submit" class="btn btn-success" >Submit</button>
                        </div>
                    </form>
                </Modal.Body>
            </Modal>
        </>
    )
}
export default LoginPage